@mixin bp($point) {
  @if $point==desktop-l {
    @media (max-width: 1920px) {
      @content;
    }
  } @else if $point==r-desktop-l {
    @media (min-width: 1920px) {
      @content;
    }
  } @else if $point==desktop-m {
    @media (max-width: 1499px) {
      @content;
    }
  } @else if $point==r-desktop-m {
    @media (min-width: 1500px) {
      @content;
    }
  } @else if $point==desktop {
    @media (max-width: 1200px) {
      @content;
    }
  } @else if $point==tablet-m {
    @media (max-width: 1024px) {
      @content;
    }
  } @else if $point==nav {
    @media (max-width: 899px) {
      @content;
    }
  } @else if $point==r-nav {
    @media (min-width: 900px) {
      @content;
    }
  } @else if $point==tablet {
    @media (max-width: 992px) {
      @content;
    }
  } @else if $point==tablet-small {
    @media (max-width: 769px) {
      @content;
    }
  } @else if $point==mobile {
    @media (max-width: 575px) {
      @content;
    }
  } @else if $point==r-desktop {
    @media (min-width: 1200px) {
      @content;
    }
  } @else if $point==r-tablet {
    @media (min-width: 992px) {
      @content;
    }
  } @else if $point==ipad-vertical {
    @media (min-width: 768px) and (max-width: 768px) {
      @content;
    }
  } @else if $point==r-tablet-small {
    @media (min-width: 769px) {
      @content;
    }
  } @else if $point==r-mobile {
    @media (min-width: 576px) {
      @content;
    }
  } @else if $point==mobile-small {
    @media (max-width: 479px) {
      @content;
    }
  } @else if $point==mobile-x-small {
    @media (max-width: 349px) {
      @content;
    }
  }
}
